*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  width: 100%;
  height: 100%;
  background-color: #141414;
}

.page__body {
  min-width: 320px;
  height: 100%;
  display: flex;
  font-size: 16px;
  line-height: 20px;
  margin: 0;
}

.page__main {
  width: 100%;
  height: 100%;
  display: flex;
}

.window {
  width: 100%;
  background-color: #141414;
}
@media (min-width: 768px) {
  .window {
    display: flex;
  }
}

.window__aside {
  min-height: 500px;
  width: 100%;
  background-color: #1a1a1a;
  padding: 15px;
  overflow-y: auto;
}
@media (min-width: 768px) {
  .window__aside {
    width: 300px;
    min-height: auto;
    flex-grow: 0;
  }
}

.tree {
  padding: 0;
  margin: 0;
  list-style: none;
  position: relative;
}

.tree__item {
  position: relative;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 25px;
}
.tree__item::before {
  content: "";
  display: block;
  height: 100%;
  width: 1px;
  position: absolute;
  left: 6px;
  top: 0;
  border-left: 1px dashed #ffffff;
}
.tree__item::after {
  content: "";
  display: block;
  height: 1px;
  width: 15px;
  position: absolute;
  left: 6px;
  top: 11px;
  border-top: 1px dashed #ffffff;
}
.tree__item--folder {
  padding-left: 0;
}

.tree__item:last-child::before {
  height: 12px;
}

.folder {
  width: 100%;
}

.folder__name {
  padding: 0;
  margin: 0;
  color: #ffffff;
  margin-left: 10px;
  padding-left: 25px;
  position: relative;
  user-select: none;
}
.folder__name::before {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../img/folder-outline-filled.svg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
.folder--show > .folder__controls > .folder__name::before {
  background-image: url("../img/folder-open-outline-filled.svg");
}

.folder__controls {
  display: flex;
  align-items: center;
}

.folder__data {
  display: none;
  padding-left: 23px;
}
.folder--show > .folder__data {
  display: block;
}

.folder__toggle {
  width: 12px;
  height: 12px;
  position: relative;
  padding: 0;
  cursor: pointer;
  background-color: #ffffff;
  border: 1px solid #141414;
  border-radius: 2px;
  z-index: 2;
}
.folder__toggle::before, .folder__toggle::after {
  display: block;
  width: 80%;
  height: 1px;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: #141414;
  transform: translate(-50%, -50%);
}
.folder__toggle::after {
  transform: translate(-50%, -50%) rotate(90deg);
}
.folder--show > .folder__controls .folder__toggle::after {
  display: none;
}

.file {
  padding: 0;
  margin: 0;
  color: #ffffff;
  color: #ffffff;
  padding-left: 21px;
  position: relative;
  user-select: none;
}
.file::before {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../img/file.svg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}